import React,{useState} from 'react';
import { Route, Switch } from 'react-router-dom';
import './films.css';
import FilmPoster from '../FilmPoster/FilmPoster';
import { data } from '../films/films-data';


const Films = () => {
	const [filmsData] = useState({data});
	return (
		<section className="films-wrapper">
			<div className="films">
				{
					filmsData.data.map((categories) =>  (
							<Switch key={categories.id}>
								{console.log(categories.id)}
								<Route exact path='/' render={()=> <FilmPoster item={categories.items} />} />
								<Route exact path={categories.routeName} render={()=><FilmPoster item={categories.items} />} />
							</Switch>
						)
					)
				}
			</div>
		</section>
	)
}


export default Films;