import React from 'react';
import { Route } from 'react-router-dom';
import './App.css';
import Header from '../src/components/header/header';
import Main from '../src/components/main/main';
import FilmPage from '../src/components/film-page/film-page';

function App() {
  return (
    <>
      <Header />
      <Main />
      <Route path='/15' component={FilmPage}/>
    </>
  );
}

export default App;
