import React from 'react';
import { useSpring, animated, config } from 'react-spring';
import './film-page.css'

const FilmPage = ({history}) =>{
	const leftBlock = useSpring({
		from: {transform: `translate3d(-35vw, 0, 0)`},
		to:{transform: `translate3d(35vw, 0, 0)`},
	});

	const rightBlock = useSpring({
		from: {transform: `translate3d(100vw, 0, 0)`},
		to:{transform: `translate3d(0, 0, 0)`},
		config: config.slow
	})
	
	return (
		<div className="film-page">
			<button className="back" onClick={history.goBack}>back</button>
			<div className="film-about">
				<animated.div style={leftBlock} className="left-block"></animated.div>
				<animated.div style={rightBlock} className="right-block"></animated.div>
			</div>
		</div>
	)
}

export default FilmPage;